<template>
  <el-card class="heightfull df ffcn">
    <el-row :gutter="10">
      <el-col :span="5">
        <el-input v-model="keywords" @keyup.enter.native="search" placeholder="请输入用户名称/手机号"></el-input>
      </el-col>
      <el-col :span="2">
        <el-button type="primary" @click="search">搜索</el-button>
      </el-col>
      <el-col :span="5" :offset="12">
        <el-button
          type="primary"
          @click="new_user"
          class="w24"
          icon="el-icon-circle-plus-outline"
        >新建用户</el-button>
      </el-col>
    </el-row>
    <el-row class="mtb10 fg1">
      <el-table
       
        :data="tableData"
        class="w24"
        @filter-change="binFilters"
        border
        max-height="490"
        :default-sort="{prop: 'userName', order: 'descending'}"
        :row-class-name="tableRowClassName"
        ref="filterTable"
      >
        <el-table-column
          prop="userName"
          label="用户名称"
          sortable
          header-align="center"
          align="center"
          width="120"
        ></el-table-column>
        <el-table-column prop="userId" label="手机号码" header-align="center" align="center"></el-table-column>
        <el-table-column
          prop="role"
          label="角色"
          :filters="filtersRole"
          column-key="role"
          :show-overflow-tooltip="true"
          header-align="center"
        ></el-table-column>
        <el-table-column
          prop="status_text"
          label="用户状态"
          :filters="[{ text: '可用', value: '1' },
        { text: '停用', value: '2' }]"
          column-key="status"
          :filter-multiple="false"
          header-align="center"
          align="center"
          width="120"
        >
          <template slot-scope="scope">
            {{scope.row.status == 1?'可用':'停用'}}
            <el-tooltip
              class="tooltip_item"
              effect="dark"
              :content="scope.row.status == 1?'停用用户':'启用用户'"
              placement="top"
            >
              <i
                :class="scope.row.status == 1?'el-icon-lock red':'el-icon-unlock blue'"
                @click="handleDelete( scope.row,scope.$index,)"
              ></i>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column
          prop="createTime"
          label="创建时间"
          sortable
          header-align="center"
          align="center"
        ></el-table-column>
        <el-table-column prop="updateTime" label="最后登录时间" header-align="center" align="center"></el-table-column>
        <el-table-column
          prop="tenantName"
          label="分公司"
          :filters="filtersOffice"
          column-key="office"
          header-align="center"
          align="center"
        ></el-table-column>
        <el-table-column label="操作" header-align="center" align="center" width="100">
          <template slot-scope="scope">
            <!-- <el-button size="mini" type="primary" @click="change_info_user(scope.row)">编辑</el-button> -->
            <el-tooltip class="tooltip_item" effect="dark" content="修改用户信息" placement="top">
              <i class="el-icon-edit blue" @click="change_info_user(scope.row)"></i>
            </el-tooltip>
            <!-- <el-button size="mini" type="primary" @click="orle_user( scope.row)">角色分配</el-button> -->
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row class="mt10 df jcfe pb10">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="current_page"
        :page-sizes="[50, 100, 200]"
        :page-size="page_size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-row>
  </el-card>
</template>

<script>
// import pagination from "@/components/pagination.vue";
export default {
  // components: { pagination },
  data() {
    return {
      tableData: [], //表数据
      filtersRole: [], //可以筛选的所有角色数组
      filtersOffice: [], //可以筛选的所有分公司数组
      filters: {
        filtersRole: [],
        filtersOffice: [],
        filtersStatus: []
      }, //已经选中的筛选数组
      keywords: "", //搜索输入
      total: 0, //总共有多少条数据
      current_page: 1, //当前选择的第几页
      page_size: 50 //当前一页选择的条数
    };
  },
  created() {
    this.getUsersList();
    this.$get("roles").then(res => {
      res.roles.forEach(role => {
        role.text = role.roleName;
        role.value = role.roleId;
      });
      this.filtersRole = res.roles;
    });
    this.$get("offices").then(res => {
      res.branchCompanys.forEach(company => {
        (company.text = company.name), (company.value = company.id);
      });
      this.filtersOffice = res.branchCompanys;
    });
  },
  mounted() {},
  methods: {
    //触发筛选
    binFilters(res) {
      if ("role" in res) this.filters.filtersRole = res.role;
      if ("status" in res) this.filters.filtersStatus = res.status;
      if ("office" in res) this.filters.filtersOffice = res.office;
      this.getUsersList();
    },
    //新建用户
    new_user() {
      this.$router.push({
        path: "/page/user/addUser",
        query: {}
      });
    },
    //修改用户信息
    change_info_user(res) {
      this.$router.push({
        path: "/page/user/editUserInfo",
        query: { id: res.id }
      });
    },
    //点击搜索时候触发
    search() {
      this.current_page = 1;
      this.$get("users/keywords", {
        param: {
          keywords: this.keywords,
          current_page: this.current_page,
          page_size: this.page_size
        }
      }).then(res => {
        this.$refs.filterTable.clearFilter();
        this.filters.filtersRole = [];
        this.filters.filtersStatus = [];
        this.filters.filtersOffice = [];
        this.tableData = res.data;
        this.total = res.total;
      });
    },
    //点击功能禁用和启用触发
    handleDelete(row, index) {
      this.$confirm(
        "确定" + (row.status == "1" ? "停用" : "启用") + "用户?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      ).then(() => {
        this.$put(
          row.status == "1" ? "users/freezeUser" : "users/unFreezeUser",
          { id: row.id },
          { "Content-Type": "application/json" }
        ).then(res => {
          this.getUsersList();
        });
      });
    },
    //遍历过滤数据
    tableRowClassName({ row, rowIndex }) {
      let role = [];
      row.userRoles.forEach(e => {
        role.push(e.roleName);
      });
      row.role = role.join(" | ");
      if (row.status == "1") {
        row.status_text = "可用";
      } else if (row.status == "2") {
        row.status_text = "停用";
      }
    },
    //该表一页的大小
    handleSizeChange(res) {
      this.page_size = res;
      this.getUsersList();
    },
    //换页
    handleCurrentChange(res) {
      this.current_page = res;
      this.getUsersList();
    },
    //筛选列表请求或者普通列表请求
    getUsersList() {
      if (
        this.filters.filtersRole.length ||
        this.filters.filtersOffice.length ||
        this.filters.filtersStatus.length
      ) {
        this.$get("users/filter", {
          param: {
            roleId: this.filters.filtersRole.join(","),
            status: this.filters.filtersStatus[0],
            tenantId: this.filters.filtersOffice.join(","),
            current_page: 1,
            page_size: this.page_size
          }
        }).then(res => {
          this.tableData = res.data;
          this.total = res.total;
          this.keywords = "";
        });
      } else {
        this.$get("users", {
          param: {
            current_page: this.current_page,
            page_size: this.page_size
          }
        }).then(res => {
          this.tableData = res.data;
          this.total = res.total;
        });
      }
    }
  }
};
</script>

<style scoped>
.red {
  font-size: 20px;
  margin-left: 17px;
  color: #fe6c6f;
}
.blue {
  font-size: 20px;
  margin-left: 17px;
  color: #409eff;
}

.tooltip_item {
  cursor: pointer;
}
</style>


